.eds-skeleton {
  display: block;
  margin: 0.2rem 0;
  height: 1rem;
  width: 100%;
}
.eds-skeleton-circle {
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
}
.eds-skeleton-rectangle {
  height: 1rem;
}
.eds-skeleton--animate {
  --background-color: #f3f3f3;
  --shimmer-highlight-color: rgba(243, 243, 243, 0.5);
  animation: shimmer 2s infinite linear;
  background: linear-gradient(to right, var(--background-color) 4%, var(--shimmer-highlight-color) 25%, var(--background-color) 36%);
  background-size: 1000px 100%;
}
.eds-contrast .eds-skeleton--animate {
  --background-color: #292b6a;
  --shimmer-highlight-color: rgba(41, 43, 106, 0.5);
}

@keyframes shimmer {
  0% {
    background-position: -1000px 0;
  }
  100% {
    background-position: 1000px 0;
  }
}
/* DO NOT CHANGE!*/
/* This file is automatically generated from @entur/tokens! Changes will be overwritten. */
.eds-loading-dots {
  height: 1em;
  width: 1em;
  display: flex;
  align-items: center;
  justify-content: center;
}
.eds-loading-dots__dot {
  display: inline-block;
  content: "";
  height: 3px;
  width: 3px;
  background-color: #181c56;
  border-radius: 50%;
  margin-right: 2px;
  transform: scale(1);
}
.eds-loading-dots__dot--blue {
  background-color: #181c56;
}
.eds-loading-dots__dot--white {
  background-color: #ffffff;
}
.eds-loading-dots__dot--one {
  -webkit-animation: dot 1s infinite;
  -webkit-animation-delay: 0s;
  animation: dot 1s infinite;
  animation-delay: 0s;
  animation-timing-function: ease-in-out;
}
.eds-loading-dots__dot--two {
  -webkit-animation: dot 1s infinite;
  -webkit-animation-delay: 0.1s;
  animation: dot 1s infinite;
  animation-delay: 0.1s;
  animation-timing-function: ease-in-out;
}
.eds-loading-dots__dot--three {
  -webkit-animation: dot 1s infinite;
  -webkit-animation-delay: 0.2s;
  animation: dot 1s infinite;
  animation-delay: 0.2s;
  animation-timing-function: ease-out;
}

@keyframes dot {
  0% {
    transform: translateY(0px);
  }
  15% {
    transform: translateY(-2px);
  }
  30% {
    transform: translateY(0px);
  }
  60% {
    transform: scale(1.3) translateY(0.5px);
  }
  100% {
    transform: scale(1) translateY(0);
  }
}
/* DO NOT CHANGE!*/
/* This file is automatically generated from @entur/tokens! Changes will be overwritten. */
@keyframes eds-back-and-forth {
  from {
    transform: none;
  }
  to {
    transform: translateX(calc(100% - 3rem));
  }
}
@keyframes grow {
  0%, 100% {
    transform: none;
  }
  50% {
    transform: scaleX(2);
  }
}
.eds-loader {
  text-align: center;
  overflow-x: hidden;
  max-width: 20rem;
  margin: 0 auto;
}
.eds-loader .eds-loader__label {
  margin-bottom: 0.5rem;
}
.eds-loader .eds-loader__percentage {
  float: right;
  margin-top: 0.5rem;
}

.eds-loader__bar {
  border: 0.125rem solid #e9e9e9;
}
.eds-loader__bar::after {
  background: #ff5959;
  content: "";
  display: block;
  height: 0.3125rem;
  transition: width 0.1s ease-out;
  width: var(--loader-width);
}
.eds-loader__bar--indeterminate {
  animation: 1s eds-back-and-forth alternate infinite ease-in-out;
  border: none;
  will-change: transform;
}
.eds-loader__bar--indeterminate::after {
  animation: 1s grow alternate infinite ease-in-out;
  width: 3rem;
  will-change: transform;
}
:root {
  --eds-loader: 1;
}
