:root {
  --reach-dialog: 1;
}

.eds-modal__overlay {
  background: rgba(0, 0, 0, 0.5);
  bottom: 0;
  display: flex;
  left: 0;
  overflow: hidden;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 30;
  animation: fadeInOverlay;
  animation-duration: 0.2s;
  animation-timing-function: ease-in-out;
}

.eds-modal__content {
  background: var(--primary-background-color);
  border-radius: 0.75rem;
  color: var(--primary-text-color);
  margin: auto;
  padding: 1.5rem;
  position: relative;
  outline-offset: 0.125rem;
  width: 100%;
  max-height: 90vh;
  overflow: auto;
  z-index: 40;
  animation: slideInContent;
  animation-duration: 0.2s;
  animation-timing-function: ease-in-out;
}
.eds-modal__content--align-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.eds-modal__content--align-end {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.eds-modal__content--size-extraSmall {
  max-width: 21rem;
  padding: 1.5rem;
}
.eds-modal__content--size-small {
  max-width: 28.125rem;
  padding: 2.5rem;
}
.eds-modal__content--size-medium {
  max-width: 45rem;
  padding: 3rem;
}
.eds-modal__content--size-large {
  max-width: 56.25rem;
  padding: 3rem;
}
.eds-modal__content--size-extraLarge {
  max-width: 78.75rem;
  padding: 3rem;
}
.eds-modal__content::-webkit-scrollbar {
  width: 16px;
}
.eds-modal__content::-webkit-scrollbar-track {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}
.eds-modal__content::-webkit-scrollbar-thumb {
  background-color: lightgrey;
  border: 4px solid transparent;
  border-radius: 8px;
  background-clip: padding-box;
}
.eds-modal__content::-webkit-scrollbar-thumb:hover {
  background-color: grey;
}

.eds-modal__close {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  border: none;
  border-radius: 0;
  font-size: inherit;
  color: inherit;
  position: absolute;
  top: 1rem;
  right: 1rem;
  padding: 0.5rem;
  cursor: pointer;
}
.eds-modal__close:focus {
  outline-offset: 0.125rem;
  outline: none;
  box-shadow: 0 0 0 0.125rem #ffffff, 0 0 0 0.25rem #181c56;
}

@keyframes fadeInOverlay {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slideInContent {
  from {
    top: 5rem;
    opacity: 0;
  }
  to {
    top: 0%;
    opacity: 1;
  }
}
